import React from 'react'
import cn from 'classnames'

import commonStyles from '../Dashboard.module.less'

import styles from './OremTable.module.less'

const COLUMNS = [
  {
    key: 'economy',
    title: ' ',
  },
  {
    key: 'month1',
    title: ' 1 (1)',
  },
  {
    key: 'month2',
    title: ' 2 (2)',
  },
  {
    key: 'total',
    title: '',
  },
]

export const OremTable = () => {
  return (
    <div className={commonStyles.table}>
      <div className={commonStyles.tableTitle}></div>

      <div className={commonStyles.tableHeaderContainer}>
        {COLUMNS.map(column => {
          return (
            <div key={column.key} className={cn(commonStyles.tableHeaderCell, styles.tableHeaderCell)}>
              {column.title}
            </div>
          )
        })}
      </div>
    </div>
  )
}
